import React from 'react';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { styles } from './styles';
import colors from '../../theme/colors';
import { NavigationService } from '../../services/NavigationService';
import { Image, View, useWindowDimensions } from 'react-native';
import { logo_extra } from '../../assets/images';
import { TabView, SceneMap } from 'react-native-tab-view';
import Review from './components/Review';
import Interview from './components/Interview';
import TabBar from './components/TabBar';

const renderScene = SceneMap({
  review: () => <Review />,
  interview: () => <Interview />,
});

const routes = [
  { key: 'review', title: 'Review' },
  { key: 'interview', title: 'Interview' },
];

const AccountActivationStatus = () => {
  const layout = useWindowDimensions();
  const [index, setIndex] = React.useState(0);

  const handlePressTab = (index: number) => {
    setIndex(index);
  };

  return (
    <BaseContainer
      backgroundStatusBarColor={colors.primary}
      backgroundColor={colors.primary}
      backgroundBottomSafeAreaColor={colors.white}
      style={styles.container}
    >
      <HeaderBase
        onPressBack={() => NavigationService.goBack()}
        containerStyle={styles.header}
        styleButtonLeft={styles.styleButtonBack}
        rightActions={[
          {
            icon: (
              <Image
                source={logo_extra}
                style={styles.logoExtra}
                resizeMode="contain"
              />
            ),
            onPress: () => {
              NavigationService.goBack();
            },
          },
        ]}
      />
      <View style={styles.contentContainer}>
        <TabView
          lazy
          navigationState={{ index, routes }}
          renderScene={renderScene}
          onIndexChange={setIndex}
          initialLayout={{ width: layout.width }}
          renderTabBar={props => (
            <TabBar handlePressTab={handlePressTab} {...props} />
          )}
        />
      </View>
    </BaseContainer>
  );
};

export default AccountActivationStatus;
